      <div class="cols rankgrid" @click="rank"><h1>排行榜</h1></div>
      <div class="cols checkgrid" @click="scanner"><h1>檢錄</h1></div>
      <div class="cols gradegrid" @click="grade"><h1>選手成績登記</h1></div>
