<template>
  <div class="listbg">
    <div class="listpage">
      <div class="page" v-for="(item,index) in 9" :key="index">{{team(index)}} 組</div>
    </div>
    <div class="listlayout">
      <div class="list" v-for="(item,index) in 10" :key="index" @click="clickmodal"></div>
    </div>
  </div>
</template>
<script>
import bus from "./bus.js";
export default {
  data() {
    return {
      teamnumber: 0
    };
  },
  methods: {
    team(index) {
      return index * 10 + 1 + "~" + (index + 1) * 10;
    },
    clickmodal: function() {
      bus.$emit("openmodal", {
        msg: true
      });
    }
  }
};
</script>
<style>
.listbg {
  width: 100%;
  height: 100%;
  display: flex;
}
.listpage {
  flex: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.page {
  width: 120px;
  height: 50px;
  background: gray;
  margin-top: 20px;
  text-align: center;
  line-height: 50px;
}
.listlayout {
  flex: 3;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.list {
  width: 440px;
  height: 120px;
  background-color: aqua;
  margin-top: 20px;
}
.list:last-child {
  margin-bottom: 20px;
}
@media screen and (max-width: 600px) {
  .listbg {
    max-width: 100%;
    height: 100%;
    display: flex;
  }
  .page {
    width: 40%;
  }
  .list {
    width: 80%;
  }
  .listlayout {
    flex: 1;
  }
  .listpage {
    flex: 1;
  }
}
@media screen and (max-width: 350px) {
  .page {
    width: 50%;
  }
  .list {
    width: 80%;
  }
}
</style>