<template>
  <div class="login">
      <div class="box">
        <h1>Dashboard</h1>

        <input
          type="text"
          name="account"
          v-model="account"
          @focus="field_focus();"
          @blur="field_blur();"
          class="account"
        />

        <input
          type="password"
          name="email"
          v-model="password"
          @focus="password_focus();"
          @blur="password_blur();"
          class="account"
        />
        <div class="signin">
          <a href="#">
            <div class="btn">Sign In</div>
          </a>
          <!-- End Btn -->
        </div>
        <!-- End Btn2 -->
      </div>
      <!-- End Box -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      account: "Account",
      password: "password"
    };
  },
  methods: {
    field_focus() {
      if (this.account == "Account") {
        this.account = "";
      }
    },
    field_blur() {
      if (this.account == "") {
        this.account = "Account";
      }
    },
    password_focus() {
      if (this.password == "password") {
        this.password = "";
      }
    },
    password_blur() {
      if (this.password == "") {
        this.password = "password";
      }
    }
  }
};
</script>
<style>
.login {
  background: #3498db;
  width: 100%;
  min-height: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.signin {
  width: 100%;
  display: flex;
  justify-content: center;
}
p {
  font-size: 12px;
  text-decoration: none;
  color: #ffffff;
}

h1 {
  font-size: 1.5em;
  color: #525252;
}

.box {
  background: white;
  width: 360px;
  border-radius: 6px;
  margin: 0 auto 0 auto;
  padding: 0px 0px 40px 0px;
  border: #2980b9 4px solid;
}

.account {
  background: #ecf0f1;
  border: #ccc 1px solid;
  border-bottom: #ccc 2px solid;
  padding: 8px;
  width: 280px;
  color: #aaaaaa;
  margin-top: 10px;
  font-size: 1em;
  border-radius: 4px;
}

.password {
  border-radius: 4px;
  background: #ecf0f1;
  border: #ccc 1px solid;
  padding: 8px;
  width: 280px;
  font-size: 1em;
}

.btn {
  background: #2ecc71;
  width: 125px;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  border-radius: 4px;
  border: #27ae60 1px solid;
  margin-top: 20px;
  margin-bottom: 20px;
  float: left;
  font-weight: 800;
  font-size: 0.8em;
}

.btn:hover {
  background: #2cc06b;
}

#btn2 {
  float: left;
  background: #3498db;
  width: 125px;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  border-radius: 4px;
  border: #2980b9 1px solid;
  margin-top: 20px;
  margin-bottom: 20px;
  margin-left: 20px;
  font-weight: 800;
  font-size: 0.8em;
}

#btn2:hover {
  background: #3594d2;
}
</style>