<template>
  <div id="flip-list-demo" class="demo">
    <button v-on:click="shuffle">Shuffle</button>
    <h1>名次</h1>
    <transition-group name="flip-list" tag="ol">
      <li v-for="item in items" v-bind:key="item" class="ranklist">{{ item }}</li>
    </transition-group>
  </div>
</template>
<script>
export default {
  data() {
    return {
      items: ["第一組", "第二組", "第三組", "第四組", "第五組", "第六組", "第七組","第八組", "第九組"]
    };
  },
  methods: {
    shuffle: function() {
      this.items = [ "第二組","第一組",  "第四組", "第五組", "第六組","第三組", "第七組","第八組", "第九組"]
    }
  }
};
</script>
<style>
.ranklist{
    background-color:#c7ccd4;
    margin-top:10px;
    padding: 10px 0 10px 0;
}
.ranklist:nth-child(1){
    background-color:gold;
}
.ranklist:nth-child(2){
    background-color:silver;
}
.ranklist:nth-child(3){
    background-color:#b87333;
}
.flip-list-move {
  transition: transform 1s;
}
</style>