<template>
  <div class="Profile" v-show="openmodal">
    <div class="profilelayout">
      <div class="profileform">
        <div class="teamname">
          <h1>第一組</h1>
          <h3>隊名:123</h3>
        </div>
        <div class="gradegroup">
          <label for="firstgrade">第一次成績</label>
          <input type="text" id="firstgrade" />
          <label for="lastgrade">第二次成績</label>
          <input type="text" id="lastgrade" />
        </div>
        <div class="imgupload">
          <input type="file" name="image" id="image" accept="image/*" @change="imageform" multiple />
          <div style="display:flex;width:100%">
            <img
              v-for="(item,index) in imageindex"
              :key="index"
              :src="imagepreview[index]"
              onerror="this.style.display='none'"
              style="width:100px;height:100px;display:inline-block"
              alt
            />
          </div>
        </div>
        <div class="checksub">
          <button>確認送出</button>
          <button @click="openmodal=!openmodal">取消</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import bus from "./bus.js";
export default {
  data() {
    return {
      openmodal: false,
      imagedata: new FormData(),
      reader: new FileReader(),
      imagepreview: [],
      imageindex: 0
    };
  },
  methods: {
    imageform(e) {
      var input = e.target.files[0];
      var url = e.target;
      this.imagedata.append(`image`, input);
      this.reader.onload = e => {
        this.imagepreview[this.imageindex] = e.target.result;
        this.imageindex++;
      };
      this.reader.readAsDataURL(url.files[0]);
    }
  },
  created() {
    bus.$on("openmodal", data => {
      this.openmodal = data.msg;
      document.documentElement.scrollTop = 0;
      document.body.scrollTop = 0
    });
  },
  beforeDestroy: function() {
    // [銷毀監聽事件]
    // 最好在组件銷毀前，清除 Event 所有監聽
    // 如果要指定哪個監聽對應方法，就需要傳入第二個參數(對應$on設定的方法)
    bus.$off("openmodal");
  },
  watch: {
    openmodal: {
      immediate: true,
      handler(newValue, oldValue) {
        if (newValue == true) {
          document.body.style.overflow = "hidden";
          document.body.style["overflow-block"] = "none";
        } else if (newValue == false) {
          document.body.style.overflow = "";
        }
      }
    }
  }
};
</script>
<style>
.Profile {
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
  display: flex;
  justify-content: center;
}
.profilelayout {
  width: 350px;
  height: 450px;
  background-color: white;
  position: absolute;
  margin-top: 5%;
  position: relative;
  border-radius: 10px;
}
.profileform {
  height: 80%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.gradegroup > input {
  width: 100%;
  margin-top: 10px;
}
.gradegroup > label {
  margin-top: 20px;
}
.gradegroup {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex: 2;
}
.teamname {
  flex: 1;
}
.teamname > h3,
h1 {
  margin: 0;
}
.imgupload {
  flex: 1;
  margin-top: 20px;
  padding-left: 55px;
}
.checksub {
  position: absolute;
  bottom: 10%;
  width: 100%;
}
.checksub > button:last-child {
  margin-left: 20px;
}
</style>